.car {
  margin: 10px;
  display: block;
  padding: 10px;
  border-radius: 10px;
  transition: border, box-shadow .5s;
}

.input {
  color: darkred;
}

.input:focus,
.input:active {
  text-decoration: none;
  outline: none;
}

.input.green {
  border: 1px solid green
}

.input.red {
  border: 1px solid red
}

.input.bold {
  border: 4px solid blue
}
